<template>
  <div>
    <div>
      <div  class="goBacks">
        <img
          src="data:image/png;base64,"
          alt v-on:click="btn"
        />
      </div>
      <div class="titles">剧照({{ list.photos ? list.photos.length : 0 }})</div>
      <!---->
    </div>
    <div class="top">
      <ul class="ul-img">
        <li class="img-list" v-for="items in list.photos" :key="items.id">
          <img :src="items" alt v-on:click="btnImgs" />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ImagePreview } from "vant";

export default {
  name: "",

  data() {
    return {
      list: [],
      photos: 0,
      ID: "",
      ImagePreview: [],
    };
  },

  methods: {
    btn(){
      this.$router.push(`/filmDetail/${ this.ID }`);
    },
    btnImgs(){
      ImagePreview(this.ImagePreview);
    }
  },
  mounted() {
    this.ID = this.$route.params.id;
    axios
      .get("https://m.maizuo.com/gateway?", {
        params: {
          filmId: this.$route.params.id,
          k: 5705222
        },
        headers: {
          "X-Client-Info":
            '{"a":"3000","ch":"1002","v":"5.0.4","e":"15605021417275674601907"}',
          "X-Host": "mall.film-ticket.film.info"
        }
      })
      .then(res => {
        if (status == 0) {
          this.list = res.data.data.film;
          this.ImagePreview = this.list.photos;
          // console.log(this.list);
        }
      });
  }
};
</script>

<style scope>
body {
  overflow-x: hidden;
}
.goBacks img {
   width: 25px;
  height: 25px;
  position: absolute;
  top: 5px;
  left: 5px;
  z-index: 999;
  /* width: 100%; */
}
.top {
  position: relative;
  /* top:40px; */
}
.titles {
  height: 30px;
  position: relative;
  top: 5px;
  left: 5px;
  width: 100%;
  text-align: center;
}
.img-list {
  /* position: relative; */
  width: 115px;
  height: 115px;
  float: left;
  margin: 0.51px;
}
.img-list img {
  width: 100%;
  height: 100%;
}
</style>



